import { Component, OnInit, ViewChild, Inject} from '@angular/core';
import { routerTransition } from '../../router.animations';
import { FormControl, FormBuilder, FormGroup, Validators} from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, VERSION, MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';

@Component({
  selector: 'app-add-block',
  templateUrl: './add-block.component.html',
  styleUrls: ['./add-block.component.scss'],
  animations: [routerTransition()]

})
export class AddBlockComponent implements OnInit {

  //Dialogbox
  version = VERSION;

  displayedColumns = ['select', 'id', 'name', 'flats', 'floors'];
  dataSource: MatTableDataSource<UserData>;
  selection = new SelectionModel<UserData>(true, []);

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private _formBuilder: FormBuilder, private dialog: MatDialog) { 

 // Create 100 users
 const users: UserData[] = [];
 for (let i = 1; i <= 3; i++) { users.push(createNewUser(i)); }

 // Assign the data to the data source for the table to render
 this.dataSource = new MatTableDataSource(users);

  }

  openMe() {
    this.dialog.open(TestDialogComponent, {
        height: '30%',
        width: '50%',
        data: {},
    })
}

  ngOnInit() {
   
  }
     

/**
 * Set the paginator and sort after the view init since this component will
 * be able to query its view for the initialized paginator and sort.
 */
ngAfterViewInit() {
  this.dataSource.paginator = this.paginator;
  this.dataSource.sort = this.sort;
}

applyFilter(filterValue: string) {
  filterValue = filterValue.trim(); // Remove whitespace
  filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
  this.dataSource.filter = filterValue;
}
      /** Whether the number of selected elements matches the total number of rows. */
      isAllSelected() {
        const numSelected = this.selection.selected.length;
        const numRows = this.dataSource.data.length;
        return numSelected == numRows;
      }
  
      /** Selects all rows if they are not all selected; otherwise clear selection. */
      masterToggle() {
        this.isAllSelected() ?
            this.selection.clear() :
            this.dataSource.data.forEach(row => this.selection.select(row));
      }
}

/** Builds and returns a new User. */
function createNewUser(id: number): UserData {
  var i= 1;
const name = NAMES[Math.round(Math.random() * (NAMES.length - i ++))];
 const flats = 
    FLATS[Math.round(FLATS.length - i++)];
return {
  id: id.toString(),
  name: name,
  flats: flats,
  floors: FLOORS[Math.round((FLOORS.length - 1))],

};
}

/** Constants used to fill up our data base. */
const FLOORS = ['6', '8', '9'];
const NAMES = ['Block A', 'BLock B', 'Block C', 'BLock D'];
const FLATS = ['24', '26', '36']


export interface UserData {
id: string;
name: string;
flats: string;
floors: string;

}


//Dialog Box Component


@Component({
  template: `
  <div matDialogTitle>Block Registration</div>
  <mat-dialog-content>
        <form [formGroup]="firstFormGroup" >
        <ng-template matStepLabel>Fill out your Details</ng-template>
        <mat-form-field class="input-full-width" fxFlex="49">
          <input matInput placeholder="Block Name" (keypress)="StringPress($event)"  formControlName="nameCtrl" required>
      
        </mat-form-field>
        
        <mat-form-field class="input-full-width" fxFlex="49">
            <input matInput placeholder="Total No Of Flats"  formControlName="flatCtrl" required>
        </mat-form-field>

        <mat-form-field class="input-full-width" fxFlex="49">
        <input matInput placeholder="Total No Of Floors"  formControlName="floorCtrl" required>
    </mat-form-field>

    </form>
  </mat-dialog-content>
  <mat-dialog-actions class="pull-right">
    <button class="btn btn-danger buttoncolor" style="color:white;" mat-button matDialogClose>Cancle</button>
        <button type="button" class="btn btn-success buttoncolor" style="color:white;"mat-button [disabled]="!firstFormGroup.valid" matDialogClose>Submit</button>

  </mat-dialog-actions>`,

})
export class TestDialogComponent implements OnInit {
  charsetOptions: any
  firstFormGroup: FormGroup;

  constructor(
    private _formBuilder: FormBuilder,
        public dialogRef: MatDialogRef<any>,
        @Inject(MAT_DIALOG_DATA) public data: any,

        
  ) {
    this.firstFormGroup = this._formBuilder.group({
      nameCtrl: ['', Validators.required],
      flatCtrl: ['', Validators.required],
      floorCtrl: ['', Validators.required]
    });

  }
  ngOnInit() {

  }
       //Only String (Name)
       StringPress(event: any) {
        const pattern = /[a-z\+\A-Z\+\-\ ]/;
    
        let inputChar = String.fromCharCode(event.charCode);
        if (event.keyCode != 8 && !pattern.test(inputChar)) {
          event.preventDefault();
        }
      }

}



