import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AddBlockComponent ,TestDialogComponent } from './add-block.component';
import { AddBlockRoutingModule } from './add-block-routing.module';
import { PageHeaderModule } from '../../shared';
import {  MatInputModule, MatButtonModule, MatSelectModule, MatCardModule, 
  MatPaginatorModule, MatTableModule, MatSortModule, MatDialogModule, MatCheckboxModule, MatTooltipModule } from '@angular/material';
import { FormsModule, ReactiveFormsModule,  } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material';
import {MatStepperModule} from '@angular/material/stepper';

@NgModule({
  imports: [
    CommonModule,
    AddBlockRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    PageHeaderModule,
    MatStepperModule,
    MatFormFieldModule,
    MatCardModule,
    MatInputModule,
    MatCheckboxModule,
    MatSelectModule,
    MatButtonModule,
    MatDialogModule,
    MatTooltipModule,
    MatPaginatorModule,
    MatTableModule,
    MatSortModule,

  ],
  entryComponents: [TestDialogComponent],
  declarations: [AddBlockComponent, TestDialogComponent]
})
export class AddBlockModule { }



